#works {
  min-height: calc(100-4rem);
  width: 100vw;
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  text-align: center;
  padding: 0 3rem;
}

.worksTitle {
  margin: 1rem 0;
  font-style: 3rem;

}

.worksDesc {
  font-weight: 300;
  font-style: 1rem; 
  max-width: 45rem;
}

.worksImgs {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100vw;
  max-width: 65rem;
}

.worksImg {
  object-fit: cover;
  height: 20rem;
  margin: .5rem;
}

.workBtn {
  padding: .5rem 2.5rem;
  border: none;
  background-color: #fffffe;
  border-radius: 2rem;
  font-size: 1rem;
  margin: 3rem 0;
}

@media screen and (max-width:585px) {
  .worksImg {
    height: 48vh;
  }
}